$border-test: 1px solid silver;

@mixin flex {
  display: flex;
  justify-content: center;
  align-items: center;
}
* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  //   background-color: aqua;
  padding: 3rem;
}

#container {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: 1fr auto;
}
aside {
  border: $border-test;
}

nav {
  border: $border-test;
  @include flex();
  justify-content: space-between;
}

.display {
  border: $border-test;
}
